body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-size: cover;
  background-image: url(https://hosted18.renlearn.com/272469/images/rp_entry_background.jpg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* SITE LOGO */
.siteLogo {
  text-align: center;
}

/* ANNOUNCEMENT */
h2 {
  font-size: 1.5em;
  letter-spacing: 0.04em;
  margin: 0;
  font-weight: normal;
  color: red;
}

.announcement {
  padding: 20px;
  border: 1px solid #e1e1e1;
  background-color: #373f43;
  color: white;
  border-radius: 4px;
  text-align: left;
}

/* WEATHER */
.weather {
  /* text-align: right; */
  float: right;
}

/* CALENDAR */
.calendar {
  text-align: center;
}

.container {
  margin: 0 auto;
  max-width: 100%;
  height: 500px;
}

.imageContainer {
  width: 100%;
  height: 100%;
  background-position: top left;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-transition: background-image 0.2s ease-in-out;
  transition: background-image 0.2s ease-in-out;
}

.image {
  width: 100%;
  height: 100%;
}

.controls {
  position: relative;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.controls__button {
  background-color: lightblue;
  border: none;
  border-radius: 2px;
  width: 50px;
  padding: 15px;
  margin: 0 10px;
  cursor: pointer;
}

